html {
  font-size: 62.5%;
  box-sizing: border-box;
  max-width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: $fuente_principal;
  font-size: 1.6rem;
  // max-width: 100%;
  // overflow-y: hidden;
}

p {
  color: $negro;
  font-size: 3rem;
  margin: 0;
  font-family: $fuente_secundaria;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
  // border: 1px solid red;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

// h3 {
//   font-family: $fuente_principal;
//   margin: 0 0 $separacion / 2 0;
//   font-weight: 900;
//   color: $link-bar;
//   text-align: center;
// }

h1 {
  font-size: 4rem;
  margin: 0;
  padding: 0;
}
h2 {
  font-size: 3.6rem;
  margin: 0;
  padding: 0;
}

h3 {
  font-size: 3rem;
  text-align: center;
  margin: 0;
  padding: 0;
}

[id] {
  scroll-margin-top: 5ex;
}

body::-webkit-scrollbar {
  width: 14px;
  background: $main-color;
}

body::-webkit-scrollbar-thumb {
  background: $link-bar;
  border-radius: 8px;
}
